import * as G from '@antv/g';
import { Animate } from '@antv/g2';
import * as _ from '@antv/util'; // 

var shapeCache;

function clipInFromCenter(shape, animateCfg) {
  var bbox = shape.getBBox();
  var centerX = bbox.minX + bbox.width / 2;
  var cliper = new G.Rect({
    attrs: {
      x: centerX,
      y: bbox.minY,
      width: 0,
      height: bbox.height
    }
  });
  shape.attr('clip', cliper);
  cliper.animate({
    width: bbox.width,
    x: bbox.minX
  }, animateCfg.duration, animateCfg.easing, function () {
    shape.attr('clip', null);
  }, animateCfg.delay);
}

clipInFromCenter.animationName = 'clipInFromCenter';
export function setShapeCache(shapes) {
  shapeCache = shapes;
}

function updateFromCenter(shape, animateCfg) {
  var fromPath = getShapeFromCache(shape).attr('path');

  var toPath = _.clone(shape.attr('path'));

  shape.attr('path', fromPath);
  shape.animate({
    path: toPath
  }, animateCfg.duration, animateCfg.easing, animateCfg.callback, 100);
}

function getShapeFromCache(shape) {
  var id = shape.id;
  var target;

  _.each(shapeCache, function (s) {
    if (s.id === id) {
      target = s;
    }
  });

  return target;
}

updateFromCenter.animationName = 'updateFromCenter';
Animate.registerAnimation('appear', 'clipInFromCenter', clipInFromCenter);
Animate.registerAnimation('update', 'updateFromCenter', updateFromCenter);